.header {
  // padding-bottom: 220px;
}

.header__top {
  padding: 24px 0 124px;
}



.header__row {
  display: flex;
  justify-content: space-between;
  align-items: center;

}


.header__list {
  display: flex;
  gap: 64px;
}



.header__link {
  color: black;
}

.header__contact {
  text-transform: uppercase;
  color: $color-orange;
}




.header__content-btn > a {
  text-transform: uppercase;
  color: $color-orange;
}

.header__title {
  width: 260px;
  margin-bottom: 8px;
}
.none {
  display: none;
}



//




.header__content-title {
  color: $color-orange;
  text-wrap: nowrap;
  font-size: 136px;

  text-align: center;
  text-transform: uppercase;

  max-width: 1112px;
}





















